/* -------------------------------------------------------------------------- 
	Header Element - Search button
  -------------------------------------------------------------------------- */

.xe-header-element-search svg,
#xe-search-form form .xe-button svg,
#xe-search-form #xe-close-search-box svg  {

    display: inline-flex;
    fill: var(--xe_base_text_color);
    width: 22px;
    height: 22px;
}

.xe-header-element-search:hover svg,
#xe-search-form form .xe-button:hover svg,
#xe-search-form #xe-close-search-box:hover svg  {

    fill: var(--xe_seconary_color);
}

/* -------------------------------------------------------------------------- 
	Search Text
  -------------------------------------------------------------------------- */

  #xe-search-form .xe-search-box-text-outer {

      @include flex();
      justify-content: center;
      margin-bottom: 20px;
  }

  #xe-search-form .xe-search-box-text-outer .xe-search-box-text {

      font-size: 18px;
      margin: 0;
  }

/* -------------------------------------------------------------------------- 
	Search form
  -------------------------------------------------------------------------- */

#xe-search-form {

    display: block;
    position: fixed;
    z-index: var(--xe_search_form_modal_zindex);
    top: -999em;
    left: 0;
    right: 0;
    width: 100%;
    min-width: 100vw;
    min-height: 200px;
    background-color: #FFFFFF;
    box-shadow: 0 4px 10px -2px rgba(0, 0, 0, 0.1);
}

// # Show search form if search button is clicked

.xe-visible-search-form #xe-search-form {

    top: 0;
    animation: xeButtonFadeDown 0.6s 0s;
}

// # Show search form if search button is clicked

#xe-search-form .xe-search-entry {

    position: relative;
    padding-left: 2%;
    padding-right: 2%;
}

#xe-search-form .search-box-header {

    position: relative;
    @include flex();
    justify-content: flex-end;
    height: 60px;
}

#xe-search-form #xe-close-search-box {

    align-items: center;
    gap: 5px;
    font-size: 10px;
    font-weight: bold;
    letter-spacing: 2px;
    text-transform: uppercase;
}

#xe-search-form #xe-close-search-box:hover {

    color: var(--xe_seconary_color);
}

#xe-search-form #xe-close-search-box svg {

    width: 14px;
    height: 14px;
}

#xe-search-form form {

    display: block;
    position: relative;
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
}

#xe-search-form form input[type="text"] {

    padding-right: 40px;
}

#xe-search-form form .xe-button {

    position: absolute;
    top: 12px;
    right: 15px;
}

/* -------------------------------------------------------------------------- 
	Search bar [no modal]
  -------------------------------------------------------------------------- */

.xe-header-element-search-bar form {

    position: relative;
    margin: 0;
}

.xe-header-element-search-bar form .xe-search-form-search-bar {

    display: inline-flex;
    align-items: center;
    height: 40px;
    padding: 8px 15px;
    border-radius: 3px;
}

.xe-header-element-search-bar .xe-search-form-submit-btn {

    position: absolute;
    top: 50%;
    right: 10px;
    height: 18px;
    width: 18px;
    fill: currentColor;
    transform: translateY(-50%);
}